// ,
const filters = {
	// 
	getIdentityNum(identify) {
		return identify.slice(0,3) + 'xxxxxxxxxxxxx' + identify.slice(16,18)
	},
	// 
	getGender(value) {
		if(value === 1) { 
			return ''
		} 
		return ''
	},
	// ,YYYY-MM-DD
	getDate(date) {
		let birthDate = new Date(date)
		const year = birthDate.getFullYear()
		let month = birthDate.getMonth() + 1
		let day = birthDate.getDate()
		if(month < 10) {
			month = '0' + month
		}
		if(day < 10) {
			day = '0' + day
		}
		return year + '-' + month + '-' + day
	},
	// ****
	getPhone(phone) {
		return phone.slice(0,3) + '****' + phone.slice(7,11)
	},
	// 
	getVisitPlanTime(num) {
		if(num === 1) {
			return '08:30~12:00'
		} 
		return '14:00~18:00'
	},
	// 
	getDay(date) {
		let currentDate = new Date(date);
		let num = currentDate.getDay();
		let day = '';
		switch(num) {
			case 0:
			day =  ''
			break;
			case 1:
			day =  ''
			break;
			case 2:
			day =  ''
			break;
			case 3:
			day =  ''
			break;
			case 4:
			day =  ''
			break;
			case 5:
			day =  ''
			break;
			case 6:
			day =  ''
			break;
		}
		return day
	},
	// 
	getTimePeriod(num) {
		let time = ''
		switch(num) {
			case 1:
			time = '08:30~09:00'
			break;
			case 2:
			time = '09:00~09:30'
			break;
			case 3:
			time = '09:30~10:00'
			break;
			case 4:
			time = '10:00~10:30'
			break;
			case 5:
			time = '10:30~11:00'
			break;
			case 6:
			time = '11:00~11:30'
			break;
			case 7:
			time = '11:30~12:00'
			break;
			case 8:
			time = '14:00~14:30'
			break;
			case 9:
			time = '14:00~15:00'
			break;
			case 10:
			time = '15:00~15:30'
			break;
			case 11:
			time = '15:30~16:00'
			break;
			case 12:
			time = '16:00~16:30'
			break;
			case 13:
			time = '16:30~17:00'
			break;
			case 14:
			time = '17:00~17:30'
			break;
			case 15:
			time = '17:30~18:00'
			break;
		}
		return time
	},
	// 
	statusText: function(value) {
		switch (value) {
			case 0:
				return '';
			case 1:
				return '';
			case 2:
				return '';
			case 3:
				return ''
		}
	},
	// 
	getNoon(num) {
		if(num === 1) {
			return ''
		} else { 
			return ''
		}
	}
}

export default filters;